//
// Progress
//


// Progress container

.progress-wrapper {
  position: relative;
  padding-top: 1.5rem;
}


// General styles

.progress {
  height: 8px;
  margin-bottom: $spacer;
  overflow: hidden;
  border-radius: $border-radius-sm;
  background-color: $progress-bg;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);

  .sr-only {
    width: auto;
    height: 20px;
    margin: 0 0 0 30px;
    left: 0;
    clip: auto;
    line-height: 20px;
    font-size: 13px;
  }
}


// Progress inner elements

.progress-heading {
  font-size: 14px;
  font-weight: 500;
  margin: 0 0 2px;
  padding: 0;
}

.progress-bar {
  box-shadow: none;
  border-radius: 0;
  height: auto;
}

.progress-info {
  margin-bottom: .5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.progress-label {
  span {
    display: inline-block;
    color: $primary;
    font-size: .625rem;
    font-weight: 600;
    text-transform: uppercase;
    background: rgba($primary, .1);
    padding: .25rem 1rem;
    border-radius: 30px;
  }
}

.progress-percentage {
  text-align: right;

  span {
    display: inline-block;
    color: $gray-600;
    font-size: .875rem;
    font-weight: 600;
  }
}
